import { createStyles, makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import React from "react";
import Box from "@material-ui/core/Box";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import VisibilityIcon from "@material-ui/icons/Visibility";
import GetAppIcon from "@material-ui/icons/GetApp";
import PublishIcon from "@material-ui/icons/Publish";

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      flexGrow: 1
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "start",
      color: theme.palette.text.secondary
    }
  })
);

const OperationArea= ({ onCheckClick, onAddClick , onModClick, onDelClick, onExpClick, onImpClick }) => {
  const classes = useStyles();

  const checkButtonHandler = (event) => {
    if (onCheckClick) {
      onCheckClick(event);
    }
  };

  const addButtonHandler = (event) => {
    if (onCheckClick) {
      onAddClick(event);
    }
  };

  const modifyButtonHandler = (event) => {
    if (onModClick) {
      onModClick();
    }
  };

  const deleteButtonHandler = (event) => {
    if (onDelClick) {
      onDelClick();
    }
  };

  const downloadButtonHandler = (event) => {
    if (onExpClick) {
      onExpClick();
    }
  };

  const importButtonHandler = (event) => {
    if (onImpClick) {
      onImpClick();
    }
  };

  return (
    <Box className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Box className={classes.paper}>
            {/*查看*/}
            <IconButton aria-label="check" onClick={checkButtonHandler}>
              <VisibilityIcon />
            </IconButton>
            {/*添加*/}
            <IconButton aria-label="add" onClick={addButtonHandler}>
              <AddIcon />
            </IconButton>
            {/*修改*/}
            <IconButton aria-label="modify" onClick={modifyButtonHandler}>
              <EditIcon />
            </IconButton>
            {/*删除*/}
            <IconButton aria-label="delete" onClick={deleteButtonHandler}>
              <DeleteIcon />
            </IconButton>
            {/*下载导出*/}
            <IconButton aria-label="download" onClick={downloadButtonHandler}>
              <GetAppIcon />
            </IconButton>
            {/*上传模板*/}
            <IconButton aria-label="import" onClick={importButtonHandler}>
              <PublishIcon />
            </IconButton>
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
};

export default OperationArea;
